import React from 'react'
import Link from 'gatsby-link'
import classnames from 'classnames'
import styled from 'styled-components'

const Logo = styled.h1`
  font-size: 1.5em;
  margin: 0;
  line-height: 1em;
  font-family: color: ${props => props.theme.monospace};
  font-weight: 400;
  color: ${props => props.theme.colors.primary};

  a {
    color: inherit;
    text-decoration: inherit;
  }

  img {
    height: 30px;
  }
`

const Header = styled.header`
  border-bottom: 1px solid #f0f0f0;
  position: ${props => (props.fixed ? 'fixed' : 'relative')};
  top: 0;
  left: 0;
  background: white;
  width: 100%;
  z-index: 99;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(1,1,1,.6);
  background-color: rgb(47, 47, 47);

  .header-main {
    width: 1200px;
    margin: 0 50px;
    height: 50px;
  }

  ul {
    padding: 0;
    list-style: none;
    margin: 0;
  }

  li {
    display: inline-block;
    margin: 0 1em;
  }

  a {
    color: inherit;
    font-weight: inherit;
    text-decoration: none;
  }

  .active {
    color: ${props => props.theme.colors.primary};
    font-weight: 600;
  }
`



export default ({ currentPath, fixed, nav }) => (
  <Offset fixed={fixed}>
    <Header fixed={fixed}>
      <div className="header-main">
        <img style={{height: '26px', marginTop: '12px'}} src="/cdn-docs/baishan-white-logo.svg" />
      </div>
    </Header>
  </Offset>
)


const Offset = ({ fixed, children }) => {
  return fixed ? (
    <div>
      {children}
      <div style={{ height: '50px' }} />
    </div>
  ) : (
    children
  )
}



